import styled from "styled-components"

export const Container = styled.div`
	display: flex;
	max-width: 400px;
	width: 100%;
	margin: 16px auto;
	border: 2px dashed rgba(0, 0, 0, 0.15);
	color: rgba(0, 0, 0, 0.4);
	cursor: pointer;
	justify-content: center;
	align-content: center;
	align-items: center;
	padding: 24px 16px;
	user-select: none;

	svg {
		margin: 0 16px;
	}
`
